
import { defineComponent, reactive } from 'vue'
import axios from 'axios'
import Swal from 'sweetalert2'
import isEmail from 'validator/lib/isEmail.js'

export default defineComponent({
  setup() {
    const reg = reactive({
      name: '',
      birthday: '',
      mail: '',
      password: '',
      isPwd: false,
      rule_sure: 'no'
    })

    const onSubmit = () => {
      const mailFormat = isEmail(reg.mail)
      const { name, birthday, mail, password } = reg
      if (mailFormat === false) {
        Swal.fire(
          '',
          'メールアドレスのフォーマットが正しくありません。',
          'warning'
        )
      } else if (mailFormat && reg.rule_sure === 'no') {
        Swal.fire('', '利用規約を同意してください。', 'warning')
      } else {
        axios
          .post(process.env.VUE_APP_API + '/users/', {
            name,
            birthday,
            mail,
            password
          })
          .then((res) => {
            if (res.data.success) {
              console.log(res)
              Swal.fire('', '登録しました!', 'success')
            }
          })
          .catch((err) => {
            console.log(err)
            if (err.response.status === 400) {
              Swal.fire('', 'メールアドレスは既に存在します。', 'warning')
            } else {
              Swal.fire('エラー', '', 'error')
            }
          })
      }
    }
    return {
      reg,
      onSubmit
    }
  }
})
